//import gift from '@/components/giftBox.vue'
import loading from '@/components/loading2.vue'
import {getCoupons,receiveCoupon} from '@/http/product.js'

export default {
	/* components:{
		gift
	}, */
	components: {
		loading
	},
	data(){
		return {
			beloading:false,
			pages:1,
			params:{
				"useStatus":0,
				"pageNum": 1,
				"pageSize": 10
			},
			couponList:[
				{
					name:'优惠券名称',
					couponStatus:0,
					amount:50,
					couponAmount:200
				},
				{
					name:'优惠券名称',
					couponStatus:1,
					price:50,
					couponAmount:200
				}
			]
		}
	},
	computed: {
	    isToken() {
	        return this.$store.state.login ? this.$store.state.login.token :''
	    }
	},
	watch:{
		isToken(){
			this.init();
		}
	},
	methods:{
		init(){
			if(this.isToken){
				this.getCouponsFun()
			}
		},
		receiveCouponFun(couponId){
			console.log('couponId---',couponId)
			receiveCoupon({couponId:couponId}).then(data=>{
				if(data.data.retCode===0){
					uni.showToast({
						title: '已领取',
						duration: 2000,
						icon:'none'
					});
					this.getCouponsFun();
				}else{
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}
			}).catch(e=>{
				uni.showToast({
					title: e.data.retMsg,
					duration: 2000,
					icon:'none'
				});
			});
		},
		getCouponsFun(){
			this.beloading = true;
			this.couponList =[];
			this.params.shopId=this.$store.state.shopId;
			getCoupons(this.params).then(data=>{
				this.beloading = false;
				if(data.data.retCode===0){
					this.couponList = data.data.result.list;
					this.pages= data.data.result.pages;
				}else{
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}
			}).catch(e=>{
				this.beloading = false;
				uni.showToast({
					title: e.data.retMsg,
					duration: 2000,
					icon:'none'
				});
			})
		}
	},
	onLoad(){
		this.init();
	},
	onShow(){
		this.init();
	},
	onReachBottom(){
		//加载下一页
		this.params.pageNum +=1;
		if(this.pages>=this.params.pageNum){
			this.getCouponsFun();
		}
	},
}
